import React from 'react';
import PropTypes from 'prop-types';

const ProjektCard = (props) => {
    const { id, borito, adatok, onClickCard } = props;
    const { cim, telepules, oszzlakszam, szabadlakszam, ingtipus, atadasev, atadashonap, atadasnegyedev, minar, maxar, minter, maxter, minszoba, maxszoba, penznem } = adatok;

    const getNegyedev = (negyev) => {
        switch (negyev) {
            case 1: {
                return 'I. negyedv';
            }
            case 2: {
                return 'II. negyedv';
            }
            case 3: {
                return 'III. negyedv';
            }
            case 4: {
                return 'IV. negyedv';
            }
            default: {
                return 'I. negyedv';
            }
        }
    };

    const getAtadasHonap = (atadho) => {
        switch (atadho) {
            case 1: {
                return 'janur';
            }
            case 2: {
                return 'februr';
            }
            case 3: {
                return 'mrcius';
            }
            case 4: {
                return 'prilis';
            }
            case 5: {
                return 'mjus';
            }
            case 6: {
                return 'jnius';
            }
            case 7: {
                return 'jlius';
            }
            case 8: {
                return 'augusztus';
            }
            case 9: {
                return 'szeptember';
            }
            case 10: {
                return 'oktber';
            }
            case 11: {
                return 'november';
            }
            case 12: {
                return 'december';
            }
            default: {
                return '';
            }
        }
    };

    /* const getTrunkedLeiras = (leir, maxLength) => {
        let result = '';
        const start = 0;
        let end = leir && leir.length ? (leir.length > maxLength ? maxLength : leir.length) : 0;
        if (end && end !== 0) {
            result = leir.substring(start, end);
        }
        if (leir && leir.length && leir.length > maxLength) {
            result = result.concat('...');
        }

        return result;
    }; */

    const getMillAr = (ar) => {
        let millAr = ar / 1000000;
        millAr = millAr.toFixed(2);

        return millAr;
    };

    return (
        <div className="projekt_card" key={id} onClick={onClickCard}>
            <div className="projekt_borito">
                <img src={borito && borito.src} alt={borito && borito.filename} />
            </div>
            {/* console.log('ADATOK: ', adatok) */}
            <div className="projekt_adatok">
                <span>
                    <strong>{cim}</strong>
                </span>
                <span>
                    Tpus: <strong>{ingtipus}</strong>
                </span>
                <span>
                    Helysg: <strong>{telepules}</strong>
                </span>
                <span>
                    r: <strong>{`${getMillAr(minar)} - ${getMillAr(maxar)} M ${penznem}`}</strong>
                </span>
                <span>
                    Mret:{' '}
                    <strong>
                        {`${minter} - ${maxter} m`}
                        <sup>2</sup>
                    </strong>
                </span>{' '}
                <span>
                    Szobk szma: <strong>{`${minszoba} - ${maxszoba} db`}</strong>
                </span>
                <span>
                    Vrhat tads: <strong>{`${atadasev}. ${atadashonap ? getAtadasHonap(atadashonap) : getNegyedev(atadasnegyedev)}`}</strong>
                </span>
                <span>
                    sszes / szabad: <strong>{`${oszzlakszam} / ${szabadlakszam} db`}</strong>
                </span>
            </div>
        </div>
    );
};

ProjektCard.propTypes = {
    borito: PropTypes.object,
    adatok: PropTypes.object.isRequired,
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
};

export default ProjektCard;
